<script>
import data from "@/assets/numbers.json"
import retrieveValue from "../src/mixins/retrieve.js"
import calculateCostAction from "../src/mixins/calculate.js"

  export default{
    mixins:[retrieveValue, calculateCostAction],
    data(){
      return{
        costs:[],
        costRecommended:["processamentoPF", ],
        email: 0,
        companies:null,
        persons:null,
        recommended: "true"
      }
    },
    
    methods: {
      tired:function(index){
        let a = parseInt(this.companies);
        let b = parseInt(this.persons);
        if(a > 0 || b > 0)
          return (this.calculateCostAction(index, 'f')*b)+(this.calculateCostAction(index, 'j')*a)
        else{
          return 0;
        }
      },
      sum:function(as,bs){
        var a = parseInt(as)
        var b = parseInt(bs)
        if(a>0 && b>0) return parseInt(a)+parseInt(b); 
        if(a === 0 && b > 0){
          return b
        }
        if(a > 0 && b === null){
          return a
        }
       return 1;
      },
      cost:function (cases, tipo) {
        let custo = [0,0,0];
        for(let i = 0; i < this.costs.length; i++){
          for(let j = 0; j < 13; j++){
            if(this.costs[i]===data[0][j].item && (data[0][j].key===cases||data[0][j].key==='fj')){
              if(data[0][j].item === 'acionamentoEmail'){
                custo[0] += data[0][j].value*this.email;
              }
              else{
                custo[0] += data[0][j].value
              }
            }
            if(this.costs[i]===data[1][j].item && (data[1][j].key===cases||data[1][j].key==='fj')){
              if(data[0][j].item === 'acionamentoEmail'){
                custo[1] += data[1][j].value*this.email;
              }
              else{
                custo[1] += data[1][j].value
              }
            }
            if(this.costs[i]===data[2][j].item && (data[2][j].key===cases||data[2][j].key==='fj')){
              if(data[0][j].item === 'acionamentoEmail'){
                custo[2] += data[2][j].value*this.email;
              }
              else{
                custo[2] += data[2][j].value
              }
            }
          }
        }
        return custo[tipo];
      }
    }
  }
</script>
<template>
<body>
    <div>
      <h1>Calculadora Mais</h1>
      <h2>Quantidade de pessoas</h2>
      <a>Pessoas Fisicas</a>
      <b-form-input v-model="persons" placeholder="Quantidade de Pessoas Fisicas" type="number"></b-form-input>
      <a>Pessoas Juridicas</a>
      <b-form-input v-model="companies" placeholder="Quantidade de Pessoas Juridicas" type="number"></b-form-input>
      <b-form-group label="Ações Recomendadas" v-slot="{ ariaDescribedby }">
      <b-form-radio v-model="recommended" :aria-describedby="ariaDescribedby" name="some-radios" value="true">Sim</b-form-radio>
      <b-form-radio v-model="recommended" :aria-describedby="ariaDescribedby" name="some-radios" value="false">Não</b-form-radio>
    </b-form-group>
    </div>
    <hr>
    <div v-if="recommended=='false'">
      <h2>Itens</h2>
      <b-form-group v-slot="{ ariaDescribedby }">
        <b-form-checkbox-group
          id="checkbox-group-1"
          v-model="costs"
          :aria-describedby="ariaDescribedby"
          name="flavour-1">

          <b-form-checkbox value="processamentoPF">Processamento de CPF</b-form-checkbox>
          <b-form-checkbox value="processamentoPJ">Processamento de CNPJ</b-form-checkbox>
          <b-form-checkbox value="enriquecimentoEmail">Enriquecimento de Emails</b-form-checkbox>
          <b-form-checkbox value="carta">Acionamento por carta</b-form-checkbox>
          <b-form-checkbox value="enriquecimentoPJ">Enriquecimento PJ</b-form-checkbox>
          <b-form-checkbox value="enriquecimentoPF">Enriquecimento PF</b-form-checkbox>
          <b-form-checkbox value="validacaoEmail">Validação de Emails</b-form-checkbox>
          <b-form-checkbox value="negativacao">Negativação</b-form-checkbox>
          <b-form-checkbox value="acionamentoWhatsapp">Acionamento por Whatsapp</b-form-checkbox>
          <b-form-checkbox value="acionamentoSms">Acionamento por SMS</b-form-checkbox>
          <b-form-checkbox value="acionamentoEmail">Acionamento por Email</b-form-checkbox>
          <b-form-input v-model="email" type="number" placeholder="Quantidade de emails"></b-form-input>
          <b-form-checkbox value="indicadoresPJ">Indicadores Pessoa Juridica</b-form-checkbox>
          <b-form-checkbox value="indicadorePF">Indicadores Pessoa Fisica</b-form-checkbox>
        </b-form-checkbox-group>
      </b-form-group>
    </div>
    <div v-else>
        <h1>Ações Recomendadas</h1>
        <h2>Acionamento por Email</h2>
        <a> R${{ 4 * retrieveValue('acionamentoEmail',0) }}/pessoa</a><br>
        <a>4 emails por pessoa.</a>
        <h2>Acionamento por SMS</h2>
        <a> R${{ 2 * retrieveValue('acionamentoSms',0) }}/pessoa</a><br>
        <a>2 SMS por pessoa.</a>
        <h2>Acionamento por Wpp</h2>
        <a> R${{ 2 * retrieveValue('acionamentoWhatsapp',0) }}/pessoa</a><br>
        <a>2 acionamentos por pessoa.</a>
        <h2>Acionamento por Carta</h2>
        <a> R${{ (retrieveValue('carta',0)).toFixed(2) }}/pessoa</a><br>
        <a>1 carta por pessoa.</a>
        <hr>
    </div>
     <div v-if="recommended == 'true'">
      <h2>Preços</h2>
      <b-table-simple hover small caption-top responsive>
        <b-thead head-variant="dark">
          <b-tr>
            <b-th colspan="2">Tipo</b-th>
            <b-th colspan="2">CPF</b-th>
            <b-th colspan="3">CNPJ</b-th>
            <b-th colspan="3">Total</b-th>
          </b-tr>
        </b-thead>
        <b-tbody>
          <b-tr>
            <b-th>Custo</b-th>
            <b-td colspan="2">R${{ calculateCostAction(0,'f').toFixed(2) }}</b-td>
            <b-td colspan="3">R${{ calculateCostAction(0,'j').toFixed(2) }}</b-td>
            <b-td colspan="4">R${{ tired(0).toFixed(2) }}</b-td>
          </b-tr>
          <b-tr>
            <b-th>Valor Minimo</b-th>
            <b-td colspan="2">R${{ calculateCostAction(1,'f').toFixed(2) }}</b-td>
            <b-td colspan="3">R${{ calculateCostAction(1,'j').toFixed(2) }}</b-td>
            <b-td colspan="4">R${{ tired(1).toFixed(2) }}</b-td>
          </b-tr>
          <b-tr>
            <b-th>Valor de Referencia</b-th>
            <b-td colspan="2">R${{ calculateCostAction(2,'f').toFixed(2) }}</b-td>
            <b-td colspan="3">R${{ calculateCostAction(2,'j').toFixed(2) }}</b-td>
            <b-td colspan="4">R${{ tired(2).toFixed(2) }}</b-td>
          </b-tr>
        </b-tbody>
      </b-table-simple>
    </div>
    <!-- 





     -->
    <div v-if="recommended == 'false'">
      <h2>Preços</h2>
      <b-table-simple hover small caption-top responsive>
        <b-thead head-variant="dark">
          <b-tr>
            <b-th colspan="2">Tipo</b-th>
            <b-th colspan="2">CPF</b-th>
            <b-th colspan="3">CNPJ</b-th>
            <b-th colspan="3">Total</b-th>
          </b-tr>
        </b-thead>
        <b-tbody>
          <b-tr>
            <b-th>Custo</b-th>
            <b-td colspan="2">R${{ cost('f',0).toFixed(2) }}</b-td>
            <b-td colspan="3">R${{ cost('j',0).toFixed(2) }}</b-td>
            <b-td colspan="4">R${{ ((cost('f',0)*persons)+(cost('j',0)*companies)).toFixed(2) }}</b-td>
          </b-tr>
          <b-tr>
            <b-th>Valor Minimo</b-th>
            <b-td colspan="2">R${{ cost('f',1).toFixed(2) }}</b-td>
            <b-td colspan="3">R${{ cost('j',1).toFixed(2) }}</b-td>
            <b-td colspan="4">R${{ ((cost('f',1)*persons)+(cost('j',1)*companies)).toFixed(2) }}</b-td>
          </b-tr>
          <b-tr>
            <b-th>Valor de Referencia</b-th>
            <b-td colspan="2">R${{ cost('f',2).toFixed(2) }}</b-td>
            <b-td colspan="3">R${{ cost('j',2).toFixed(2) }}</b-td>
            <b-td colspan="4">R${{ ((cost('f',2)*persons)+(cost('j',2)*companies)).toFixed(2) }}</b-td>
          </b-tr>
          
        </b-tbody>
      </b-table-simple>
    </div>
</body>
</template>
